export default {
  name: 'ApplicationLayout',
  components: {
    // DropdawnSelect,
    // Timer,
  },

  data() {
    return {
      text: '',
      // time: 0,
      period: 0,
    };
  },
  methods: {
    isSelected(value) {
      this.$data.period = parseInt(value, 10);
    },

    playSignal() {
      const audio = new Audio(require('@/assets/signal.mp3'));
      audio.play();
    },

    startCount() {
      if (this.period > 0) {
        this.$store.dispatch('startTimer', { cap: this.period });
      }
    },
  },
  computed: {
    time() {
      return this.$store.state.timerCounter;
    },
  },
  watch: {
  },
};
</script>
